import React, { useState } from "react";
import * as XLSX from "xlsx";
import { db } from "../services/firebaseConfig";
import { doc, setDoc } from "firebase/firestore";
import InputForm from "../components/inputForm/inputForm";
import Button from "../components/button/Button";


const Input = () => {
  const [excelData, setExcelData] = useState([]);
  const [selectedCourse, setSelectedCourse] = useState("");
  const [expeditionDate, setExpeditionDate] = useState("");

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();

    reader.onload = (event) => {
      const binaryStr = event.target.result;
      const workbook = XLSX.read(binaryStr, { type: "binary" });
      const sheetName = workbook.SheetNames[0];
      const sheet = workbook.Sheets[sheetName];
      const jsonData = XLSX.utils.sheet_to_json(sheet);

      setExcelData(jsonData);
    };

    reader.readAsArrayBuffer(file);
  };

  const handleSubmit = async () => {
    if (!selectedCourse || !expeditionDate) {
      alert("Por favor, selecione o curso e insira a data de expedição.");
      return;
    }

    if (excelData.length === 0) {
      alert("Nenhum dado carregado da planilha.");
      return;
    }

    const filteredData = excelData.map((item) => ({
      Curso: selectedCourse,
      DataExpedicao: expeditionDate,
      Numero: item["Número"] || "",
      Matricula: item["Matricula"] || "",
      Nome: item["Nome"] || "",
      RG: item["RG"] || "",
    }));

    try {
      const batchPromises = filteredData.map(async (item) => {
        const docRef = doc(db, "cadastros", String(item.Numero));
        await setDoc(docRef, item);
      });

      // Aguarda todas as inserções serem concluídas
      await Promise.all(batchPromises);

      alert("Documentos inseridos com sucesso no Firestore!");
    } catch (error) {
      console.error("Erro ao inserir documentos no Firestore:", error);
      alert("Erro ao inserir documentos. Verifique o console.");
    }
  };

  const handleDownload = () => {
    if (excelData.length === 0) {
      alert("Nenhum dado carregado para exportação.");
      return;
    }

    const exportData = excelData.map((item) => {
      // Selecionar os dados obrigatórios
      const row = {
        Numero: item["Número"] || "",
        Matricula: item["Matricula"] || "",
        Nome: item["Nome"] || "",
        RG: item["RG"] || "",
      };

      // Verificar e adicionar os dados opcionais, caso existam
      if (item["Título de TCC"]) row["Título de TCC"] = item["Título de TCC"];
      if (item["Nome do orientador"]) row["Nome do orientador"] = item["Nome do orientador"];
      if (item["Nota do TCC"]) row["Nota do TCC"] = item["Nota do TCC"];

      // Adicionar todas as notas presentes na planilha
      Object.keys(item).forEach((key) => {
        if (key.toLowerCase().includes("nota")) {
          row[key] = item[key];
        }
      });

      return row;
    });

    // Gerar o arquivo Excel
    const worksheet = XLSX.utils.json_to_sheet(exportData);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Dados Exportados");

    // Download do arquivo
    XLSX.writeFile(workbook, "dados_exportados.xlsx");
  };

  const filteredData = excelData.map((item) => ({
    Numero: item["Número"] || "",
    Matricula: item["Matricula"] || "",
    Nome: item["Nome"] || "",
    RG: item["RG"] || "",
  }));
  
  return (
    <div className="input">
      <h1>Importar Dados de Alunos</h1>
      <div className="form">
        
        <div className="form-2">
          <div>
            <label htmlFor="course">Selecionar Excel</label>
            <InputForm
              type="file"
              accept=".xlsx, .xls"
              onChange={handleFileUpload}
            />
          </div>
          <div>
            <label htmlFor="course">Selecionar Curso:</label>
            <select
              id="course"
              value={selectedCourse}
              onChange={(e) => setSelectedCourse(e.target.value)}
            >
              <option value="">Selecione</option>
              <option value="Pós Graduação em Advocacia Trabalhista">Pós Graduação em Advocacia Trabalhista</option>
              <option value="Pós Graduação em Advocacia Criminal">Pós Graduação em Advocacia Criminal</option>
              <option value="Pós Graduação em Advocacia Eleitoral">Pós Graduação em Advocacia Eleitoral</option>
            </select>
          </div>

          <div>
            <label htmlFor="date">Data de Expedição:</label>
            <input
              type="date"
              id="date"
              value={expeditionDate}
              onChange={(e) => setExpeditionDate(e.target.value)}
            />
          </div>
        </div>
        <br/>
        <div className="form-2">
          <Button className="btn-solicitar" onClick={handleSubmit} label="Enviar Dados"/>
          <Button className="btn-solicitar" onClick={handleDownload} label="Download da Planilha"/>
        </div>
      </div>
      {filteredData.length > 0 && (
        <div>
          <h3>Pré-visualização dos Dados</h3>
          <table>
            <thead>
              <tr>
                <th>Curso</th>
                <th>Data de Expedição</th>
                <th>Número</th>
                <th>Matrícula</th>
                <th>Nome</th>
                <th>RG</th>
              </tr>
            </thead>
            <tbody>
              {filteredData.map((row, index) => (
                <tr key={index}>
                  <td>{selectedCourse}</td>
                  <td>{expeditionDate}</td>
                  <td>{row["Numero"]}</td>
                  <td>{row["Matricula"]}</td>
                  <td>{row["Nome"]}</td>
                  <td>{row["RG"]}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default Input;
